import React from 'react'
import styled from 'styled-components'
import {Link} from 'gatsby'
import FooterImg from '../assets/images/pinheiro-footer.png'
import { AiFillTwitterSquare } from "react-icons/ai"

const Footer = () => {
    return (
        <FooterContainer>
        <FooterDesc src={FooterImg} />    
                          
                
                <FooterLinkItems>
                    <FooterLinkTitle>Endereço:</FooterLinkTitle>
                    <FooterLink>Praça dos Três Poderes - Brasília - DF</FooterLink>
                    <FooterLink>CEP: 70175-900</FooterLink>
                    <FooterLink>(61) 3217 - 3000</FooterLink>
                </FooterLinkItems>
            
            
                

                <FooterLinkItems>
                    <FooterLinkTitle>Mídias Sociais</FooterLinkTitle>
                    <FooterIcon to="https://twitter.com/nauebernardo"><AiFillTwitterSquare /></FooterIcon>
                </FooterLinkItems>

        </FooterContainer>
    )
}

export default Footer


const FooterContainer = styled.div`
    padding: 5rem calc((100vw - 1100px) /2);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    color: #fff;
    background: rgb(22,40,54);

    @media screen and (max-width: 800px) {
        grid-template-columns: 1fr;
        justify-items: left;
        padding: 3rem;
        row-gap: 30px;
    }
`
const FooterDesc = styled.img`
    height: 100%;
    

    @media screen and (max-width: 800px) {
        width: 65%;
        justify-self: center;
    }
`

const FooterLinkItems = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
`
const FooterLinkTitle = styled.h2`
    font-size: 18px;
    margin-bottom: 16px;
`
const FooterLink = styled(Link)`
    text-decoration: none;
    margin-bottom: 0.5rem;
    font-size: 16px;
    color: #fff;

    &:hover {
        color: rgb(215,187,161);
        transition: 0.3s ease-out;        
    }
`
const FooterIcon = styled(Link)`
    text-decoration: none;
    margin-bottom: 0.5rem;
    font-size: 40px;
    color: #fff;

    &:hover {
        color: rgb(215,187,161);
        transition: 0.3s ease-out;        
    }
`